import {
  Box,
  Checkbox,
  Collapse,
  Flex,
  Grid,
  GridItem,
  LightMode,
  Popover,
  PopoverArrow,
  PopoverBody,
  PopoverContent,
  PopoverHeader,
  PopoverTrigger,
  Progress,
  Stat,
  StatLabel,
  StatNumber,
  Tooltip,
  useColorModeValue
} from "@chakra-ui/react";
import React from "react";
import { GraphContext, UserContext } from "../Contexts";
const Footer = () => {
  const { logged, user } = React.useContext(UserContext);
  const { promedio, creditos, toggleCheckbox } = React.useContext(GraphContext);

  return (
    <Collapse in={logged} key={user.carrera?.id} position="relative">
      <Flex
        alignItems="center"
        bg={useColorModeValue("headerbg", "headerbgdark")}
      >
        <Box>
          <Tooltip
            label={
              creditos.reduce((acc, node) => {
                acc += node.creditos;
                return acc;
              }, 0) +
              " de " +
              user.carrera?.creditos.total +
              " crditos"
            }
          >
            <Stat p="0.4em" color="white" size="sm">
              <StatLabel>Crditos</StatLabel>
              <StatNumber>
                {creditos.length !== 0
                  ? (
                      (creditos.reduce((acc, node) => {
                        acc += node.creditos;
                        return acc;
                      }, 0) /
                        user.carrera?.creditos.total) *
                      100
                    ).toFixed(2)
                  : 0}
                %
              </StatNumber>
            </Stat>
          </Tooltip>
        </Box>
        <Grid
          flexGrow={1}
          mx={5}
          columns={creditos.length}
          templateColumns="repeat(10, 1fr)"
        >
          {creditos.map((c) => (
            <GridItem colSpan={c.proportion}>
              <Popover placement="top" trigger="hover">
                <LightMode>
                  <PopoverTrigger>
                    <Box>
                      <Progress
                        hasStripe
                        css={{
                          backgroundColor: c.bg,
                        }}
                        max={c.creditosNecesarios}
                        value={c.creditos}
                        colorScheme={c.color}
                      />
                    </Box>
                  </PopoverTrigger>
                </LightMode>
                <PopoverContent>
                  <PopoverArrow />
                  <PopoverHeader border="none">
                    <strong>{c.nombre}</strong>
                  </PopoverHeader>
                  <PopoverBody>
                    {c.checkbox ? (
                      <LightMode>
                        <Checkbox
                          isIndeterminate={!!!c.check}
                          isChecked={!!c.check}
                          colorScheme={c.color}
                          onClick={(e) => {
                            e.preventDefault();
                            toggleCheckbox(c);
                          }}
                        >
                          Marcar como completo
                        </Checkbox>
                      </LightMode>
                    ) : c.creditosNecesarios ? (
                      `${c.creditos} de ${c.creditosNecesarios} crditos necesarios.`
                    ) : (
                      `Tens ${c.creditos} crditos.
                      Eleg entre tesis y tpp para saber cuantos necesits.`
                    )}
                  </PopoverBody>
                </PopoverContent>
              </Popover>
            </GridItem>
          ))}
        </Grid>
        <Box>
          <Stat p="0.4em" color="white" size="sm" textAlign="right">
            <StatLabel>Promedio</StatLabel>
            <StatNumber>{promedio}</StatNumber>
          </Stat>
        </Box>
      </Flex>
    </Collapse>
  );
};

export default Footer;
